function formatNums(el) {
    let num = +$(el).attr("data-num")
    $(el).html(num.toLocaleString())
}

$(".cart-row").each(function (i,el) {
    let input = $(el).find(".cart-row__count input")
    let price = $(el).find("._price")
    let weight = $(el).find("._weight")
    let priceSum = $(el).find("._price-sum")
    let weightSum = $(el).find("._weight-sum")

    formatNums(price)
    formatNums(weight)
    formatNums(priceSum)
    formatNums(weightSum)

    $(input).change(function () {
        let val = $(this).val()
        let allSum = +$(price).attr("data-num") * val
        let allWeight = +$(weight).attr("data-num") * val
        $(priceSum).attr("data-num",allSum)
        $(weightSum).attr("data-num",allWeight)
        formatNums(priceSum)
        formatNums(weightSum)
    })
})

$("._make-order").click(function () {
    $(".cart-order ._view1").removeClass("_active")
    $(".cart-order ._view2").addClass("_active")
})
